  margin: 0 auto;
  padding-left: 20px;
  font-family: Poppins, sans-serif;
}

.slide_1__mobile {
  display: none;
}

.main {
  max-width: 1170px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding-top: 80px;
  display: flex;
}

.main__text__title {
  font-size: 52px;
  font-weight: 700;
  line-height: 64px;
}

.main__text__subtitle {
  padding-top: 48px;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.main__button {
  width: 173px;
  height: 52px;
  background-color: #f2cf7e;
  border: none;
  border-radius: 50px;
  margin-top: 52px;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}

.slide_2 {
  max-width: 1170px;
  margin: 0 auto;
  padding-bottom: 80px;
  display: flex;
}

.functions {
  width: 900px;
}

.functions__title {
  padding-bottom: 30px;
  font-size: 18px;
  font-weight: 600;
  line-height: 24px;
}

.functions__block {
  grid-template-areas: "one two"
                       "three four";
  display: grid;
}

.functions__block__item__1 {
  grid-area: one;
}

.functions__block__item__2 {
  grid-area: two;
}

.functions__block__item__3 {
  grid-area: three;
}

.functions__block__item__4 {
  grid-area: four;
}

.rest_mobile, .map_mobile, .qr_mobile, .bonus_mobile {
  display: none;
}

.function__content {
  align-items: center;
  display: flex;
}

.function__item__text {
  padding-left: 25px;
}

.function__item__head {
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
}

.function__item__disc {
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
}

#f_3, #f_4 {
  margin-left: 10px;
}

.space {
  width: 270px;
  background-color: #2b146c;
  border: none;
}

.logo:hover {
  cursor: pointer;
}

.android_icon:hover, .ios_icon:hover, .main__button:hover {
  cursor: pointer;
  box-shadow: 0 0 5px #fff;
}

.header_text__item {
  cursor: pointer;
  text-decoration: none;
  position: relative;
}

.header_text__item:before {
  content: "";
  width: 0;
  height: 1.5px;
  background-color: #f2cf7e;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.header_text__item:hover:before {
  width: 100%;
}

.header_text__item:before {
  transition: width .5s;
}

.logo:hover {
  animation: pulses 1.5s ease-in-out infinite;
}

@keyframes pulses {
  0% {
    opacity: .7;
    transform: scale(.9);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: .7;
    transform: scale(.9);
  }
}

